.add-review-box {
    border-top: 1px solid #4642422e;
}
.w100 {
    width: 100%;
}
.d-inline-flex {
    display: -ms-inline-flexbox;
    display: inline-flex;
}
.add-comment div .input-box {
    width: calc(100% - 61px);
    margin-left: 23px;
}
.add-comment div .input-box textarea {
    height: auto;
    resize: auto;
    -webkit-appearance: none;
    padding: 15px 20px 15px 55px;
}

.add-comment div .user-avatar img {
    width: 46px;
    height: 46px;
    border-radius: 50%;
}


