.provider-section {
    height: 368px;
    padding: 0 !important;
}
.provider-section .bg {
    background-size: contain;
}
.provider-section .group-button {
    position: relative;
}
.provider-section .group-button button {
    position: absolute;
    z-index: 99;
    color: #fff;
    padding: 7px 12px;
    margin-right: 10px;
    border: 1px solid rgba(255,255,255,0.41);
    border-radius: 4px;
    background: rgba(255,255,255,0.11);
}
.provider-section .group-button button i {
    margin-right: 10px;
}
.group-button button.view-photos {
    top: 300px;
    left: 30px;
    cursor: pointer;
}
.group-button button.share {
    top: 30px;
    right: 100px;
}
.group-button button.save {
    top: 30px;
    right: 0;
}
.list-single-section .bg.par-elem {
    /*transform: translateZ(0px) translateY(22.6369%);*/
}
.d-inline-flex {
    display: -ms-inline-flexbox;
    display: inline-flex;
}
.list-single-header-contacts ul {
    display: grid;
    text-align: left;
}
.list-single-header-contacts li {
    margin-top: 20px;
}
h3.box-header {
    border-bottom: 1px dashed #334e6f59;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    padding-bottom: 15px;
}
.map-container {
    margin-bottom: 20px;
    height: 300px;
}
.item-provider {
    margin-right: 10px;
}
.item-provider span {
    margin-left: 3px;
}
.item-provider i {
    color: #1c1f30;
}
.description p {
    color: #878C9F;
    margin-top: 10px;

}
.scroll-to-fixed-fixed {
    z-index: 12;
    position: fixed;
    top: 70px;
    width: 100%;
    left: 0;
}


