.product-item {
    padding: 4px;
    margin: 5px;
    border-radius: 10px;
    border: 1px solid #eee;
    cursor: pointer;
}
.product-item .team-photo img.respimg {
    height: 141px;
}
.product-name {
    color: #333;
}
.col-md-5.info-product {
    text-align: left;
}
.select-type-product .ant-radio-wrapper span {
    color: #000000a6;
}
.select-type-product .ant-radio-wrapper .ant-radio .ant-radio-checked .ant-radio-inner {
    border-color: #ff595e
}
.price {
    font-size: 30px;
}
.info-product .line {
    margin-top: 20px;
    border-bottom: 1px solid #EFF0F5;
}
.info-product .desc-product {
    margin-top: 15px;
}


