.view-gallery-modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    overflow: auto;
    height: 100%;
    z-index: 1000;
    /* display: none; */
    -webkit-transform: translate3d(0,0,0);
    background: rgba(0,0,0,0.81);
}
.view-gallery-modal .icon {
    position: absolute;
    right: 20px;
    top: 10px;
    color: #fff;
    font-weight: 100;
    cursor: pointer;
    font-size: 40px;
}
.view-gallery-modal section {
    background: none;
    margin: 40px 0 40px 6%;
    width: 88%;
}


